import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { updateUser, apiRequest } from '../actions/user-actions';
import { dataRequest } from '../actions/pressmarks-actions';
import PressmarkSearch from '../components/PressmarkSearch';
import PressmarkList from '../components/PressmarkList';
import logo from '../10image.png'




class App extends Component {

  constructor(props) {
    super(props);
    this.onUpdateUser = this.onUpdateUser.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount() {
    setTimeout(() => {
      this.props.onApiRequest();
    }, 2000)
  }

  
  onSubmit = (searchValue) => {
    this.props.dataRequest(searchValue); 
  };





  onUpdateUser(event) {
    this.props.onUpdateUser(event.target.value);
  }


  render() {

    return (

      <div>
        <input onChange={this.onUpdateUser} />
        <div>{this.props.user}</div>
        <PressmarkSearch onSubmit={this.onSubmit}/>
        <PressmarkList pressmarks={this.props.pressmarks}/>
        <img src={logo} alt=""/>
      </div>

    );
  }
}

const mapStateToProps = (state, props) => {
  return {
    products: state.products,
    user: state.user,
    pressmarks: state.pressmarks
  }
};

const mapActionsToProps = (dispatch, props) => {

  return bindActionCreators(
    {
      onUpdateUser: updateUser,
      onApiRequest: apiRequest,
      dataRequest: dataRequest

    }, dispatch
  )
}



export default connect(mapStateToProps, mapActionsToProps)(App);

